<template>
  <div class="trash">
    <div class="container">
    <el-card class="box-card" v-for="item in trashList" :key="item.id">
      <div slot="header" class="clearfix">
        <span>{{item.title}}</span>
        <div class="trash-header-right" style="float: right; padding: 3px 0">
          <el-tooltip class="item" effect="dark" content="恢复" placement="top-start">
            <i
            class="el-icon-refresh"
            style="color: #21b1ed;font-size: 14px;margin-right: 10px;cursor: pointer;"
            @click="handleRevert(item.id)"
            ></i>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" content="删除" placement="top-start">
            <i
              class="el-icon-delete"
              style="color: red;font-size: 14px;cursor: pointer"
              @click="handleDelete(item.id)"
            ></i>
          </el-tooltip>
        </div>
      </div>
      <div class="trash-content">
        {{item.content}}
      </div>
    </el-card>
    </div>
  </div>
</template>

<script>
import Trash from '@/apis/trash'
export default {
  data() {
    return {
      trashList: []
    }
  },
  created(){
    Trash.getAll().then(res=>{
      console.log(res.data)
      this.trashList = res.data
    })
  },
  methods: {
    handleRevert(noteId){
      this.$confirm("笔记将恢复到所属笔记本", "恢复笔记").then(()=>{
        Trash.revert(noteId).then(res=>{
          this.$message.success('恢复成功')
          this.trashList = this.trashList.filter(item=>item.id !==noteId)
        }).catch(err=>{
          this.$message.error(res.msg)
        })
      })
    },
    handleDelete(noteId){
      this.$confirm("删除笔记后将不可恢复", "删除笔记").then(()=>{
        Trash.delete(noteId).then(res=>{
          this.$message.success('删除成功')
          this.trashList = this.trashList.filter(item=>item.id !==noteId)
        }).catch(err=>{
          this.$message.error(res.msg)
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
.container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  .box-card {
    width: 23%;
    margin-bottom: 16px;
    .trash-content {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 5; //  从第几行开始省略
      overflow: hidden;
      line-height: 26px;
    }
  }
}
</style>



